import LoginForm from "@/components/auth/LoginForm";
import { DEMO_BUTTONS } from "@/lib/constants";

export default function LoginPage() {
  return (
    <div className="min-h-screen flex">
      <div className="flex-1 flex items-center justify-center p-8">
        <LoginForm />
      </div>
      <div className="flex-1 bg-gradient-to-br from-black to-gray-900 hidden lg:flex flex-col items-center justify-center p-12 space-y-6">
        {DEMO_BUTTONS.map((button, index: number) => (
          <button
            key={index}
            className="w-full max-w-xs bg-gray-800 hover:bg-gray-700 text-white py-4 px-6 rounded-lg flex items-center justify-start space-x-4 transition-colors duration-200"
          >
            <button.icon className="h-5 w-5" />
            <span>{button.name}</span>
          </button>
        ))}
      </div>
      <div className="absolute bottom-4 left-0 right-0 text-center text-gray-500 text-sm">
        <p>All rights reserved Â© {new Date().getFullYear()} Forecast</p>
        <p className="mt-1">Created by Your Team</p>
      </div>
    </div>
  );
}